    // box-sizing: border-box
}

#h2o-editor {
    padding: 20px;
    // background-color: aliceblue;

    .focusBlock{
        background-color: whitesmoke;
        border-radius: 10px;
        border: 1px dashed gray;
        margin: 5px;
    }
    .nonFocusBlock{
        border: 1px dashed transparent;
        margin: 5px;
    }

    p {
        font-size: 1.3em;
        padding: 1em;
        margin: 0;
        text-indent: 2em;
        line-height: 1.5em;
    }
}